@charset "UTF-8";
:root {
  --clip: left center;
  --size: 0;
  --background-thumb: #2b3769;
  --scrollbar-thumb-bgcolor: #757575;
  --shadow-size: 0px 0px 10px 1px rgba(0, 0, 0, 0.4);
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

.masked-input {
  color: transparent; /* Скрыть текст, пока маска не активна */
}

.masked-input::placeholder {
  color: black; /* Цвет плейсхолдера */
}

.gradient-preloader {
  background: linear-gradient(90deg, #2b3769 -23.739%, #93a9ff 96.778%);
}

@media (min-width: 1440.99px) {
  .underline:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline:hover::before {
    width: 47%;
  }
  .underline:hover::after {
    width: 47%;
  }
  .underline::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline-w:hover::before {
    width: 47%;
  }
  .underline-w:hover::after {
    width: 47%;
  }
  .underline-w::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
}
.services-price__title {
  text-align: center;
  margin-bottom: 70px;
}
@media screen and (max-width: 690px) {
  .services-price__title {
    margin-bottom: 40px;
  }
}
.services-price__content {
  display: grid;
  height: 100%;
  gap: 60px;
  justify-content: space-between;
  grid-template-columns: repeat(3, minmax(200px, 1fr));
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  .services-price__content {
    gap: calc(20px + 40 * (100vw - 1440px) / 480);
  }
}
@media screen and (max-width: 1440px) {
  .services-price__content {
    gap: 20px;
  }
}
@media screen and (max-width: 1300px) {
  .services-price__content {
    grid-template-columns: repeat(2, minmax(200px, 51fr));
  }
}
@media screen and (max-width: 640px) {
  .services-price__content {
    gap: 20px;
    grid-template-columns: repeat(1, minmax(200px, 1fr));
  }
}
.services-price__column {
  border-radius: 30px;
  overflow: hidden;
}
@media screen and (max-width: 768px) {
  .services-price__column {
    border-radius: 15px;
  }
}
.services-price__column._open .card-price__content {
  grid-template-rows: 1fr;
  height: 100%;
}
.services-price__column._open .card-price__button {
  display: flex;
}
.services-price__column._open .card-price__open-btn {
  display: none;
}
.services-price__column._open .card-price__footer {
  flex-direction: column;
}
.services-price__column._open .card-price__close-btn {
  display: block;
  opacity: 1;
}
.services-price__card {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 20px;
  background: url("../img/main/card-item-bg.png") center/cover no-repeat;
}
@media screen and (max-width: 490px) {
  .services-price__card {
    padding: 10px 10px 18px;
  }
}

.card-price__title {
  line-height: 122.857143%; /* 43px/35 */
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card-price__close-btn {
  cursor: pointer;
  opacity: 0;
  transition: all 0.4s ease 0s;
  transform: rotate(45deg);
}
.card-price__close-btn ._icon::before {
  font-size: 2.8rem;
  color: #2b3769;
}
.card-price__open-btn {
  display: flex;
  align-items: center;
  justify-content: center;
}
.card-price__content {
  flex: 1 1 auto;
}
@media screen and (max-width: 490px) {
  .card-price__content {
    overflow: hidden;
    display: grid;
    grid-template-rows: 0fr;
    transition: grid-template-rows 0.4s ease 0s;
  }
}
.card-price__list {
  min-height: 0;
  overflow: hidden;
}
.card-price__item {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.card-price__item:not(:last-child) {
  margin-bottom: 14px;
}
.card-price__item:not(:last-child) {
  margin-bottom: 10px;
}
.card-price__item:first-child {
  margin-top: 14px;
}
.card-price__icon {
  margin-right: 11px;
}
.card-price__text {
  font-family: "averta-regular", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 125%; /* 20px/16 */
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .card-price__text {
    font-size: calc(12px + 4 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .card-price__text {
    font-size: 12px;
  }
}
.card-price__footer {
  margin-top: 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 640.98px) and (max-width: 1710px) {
  .card-price__footer {
    flex-direction: column;
  }
}
.card-price__price {
  font-size: 25px;
  font-weight: 400;
  line-height: 124%; /* 31px/25 */
  white-space: nowrap;
}
@media screen and (max-width: 1710px) {
  .card-price__price {
    align-self: start;
  }
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .card-price__price {
    font-size: calc(20px + 5 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .card-price__price {
    font-size: 20px;
  }
}
.card-price__button {
  margin-top: 20px;
  align-self: end;
  background: #fff;
}
.card-price__button::before {
  background: #2b3769;
}
.card-price__button:hover .icon {
  transform: rotate(-45deg);
}
@media (min-width: 490.98px) and (max-width: 640px) {
  .card-price__button {
    margin-top: 0;
  }
}
@media screen and (max-width: 490px) {
  .card-price__button {
    display: none;
    align-self: center;
  }
}
.card-price__icon-btn .icon-arrow-right::before {
  color: #fff;
}
.card-price__open-btn {
  color: #fff;
}
@media (min-width: 490.99px) {
  .card-price__open-btn {
    display: none;
  }
}